:root {
  /* Colors */
  --color-primary: #55c57a;
  --color-primary-light: #7ed56f;
  --color-primary-dark: #28b485;

  --color-secondary: #ffb900;
  --color-secondary-light: #ffcf40;
  --color-secondary-dark: #ff7730;

  --color-tertiary: #2998ff;
  --color-tertiary-light: #5aafff;
  --color-tertiary-dark: #0066cc;

  /* Grays */
  --color-gray-light-1: #f7f7f7;
  --color-gray-light-2: #eee;
  --color-gray-dark-1: #777;
  --color-gray-dark-2: #999;
  --color-gray-dark-3: #333;

  /* Basic colors */
  --color-white: #fff;
  --color-black: #000;

  /* Grid variables */
  --grid-width: 114rem;
  --gutter-vertical: 8rem;
  --gutter-horizontal: 6rem;

  /* Other variables */
  --default-font-size: 1.6rem;
}